import { useEffect, useState, useMemo, useCallback } from 'react';
import { useRouter } from 'next/router';
import DashboardLayout from '../../components/layout/DashboardLayout';
import { api } from '../../services/api';
import { useToast } from '../../components/ui/ToastProvider';
import styles from './product-sales.module.css';

interface ProductSalesRow {
  productId: string;
  productName: string;
  category: string;
  unitsSold: number;
  revenue: number;
  avgSellingPrice: number;
  ordersCount: number;
  currentStock: number;
  status: 'in-stock' | 'low-stock' | 'out-of-stock';
}

interface KPIData {
  totalUnitsSold: number;
  totalRevenue: number;
  bestSellingProduct: string;
  zeroSalesProducts: number;
  cancellationRate: number;
}

interface AnalyticsResponse {
  kpis: KPIData;
  products: ProductSalesRow[];
  pagination: {
    page: number;
    limit: number;
    total: number;
    pages: number;
  };
}

export default function ProductSalesPage() {
  const router = useRouter();
  const { pushToast } = useToast();
  const [vendorId, setVendorId] = useState('');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  const [data, setData] = useState<AnalyticsResponse | null>(null);

  const [fromDate, setFromDate] = useState('');
  const [toDate, setToDate] = useState('');
  const [search, setSearch] = useState('');
  const [debouncedSearch, setDebouncedSearch] = useState('');
  const [category, setCategory] = useState('');
  const [stockStatus, setStockStatus] = useState('');
  const [minUnits, setMinUnits] = useState('');
  const [minRevenue, setMinRevenue] = useState('');
  const [zeroSalesOnly, setZeroSalesOnly] = useState(false);

  const [sortBy, setSortBy] = useState('unitsSold');
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('desc');
  const [page, setPage] = useState(1);
  const limit = 20;

  useEffect(() => {
    if (typeof window !== 'undefined') {
      const stored = localStorage.getItem('cc_user');
      if (stored) {
        try {
          const parsed = JSON.parse(stored);
          setVendorId(parsed.id || '');
        } catch {}
      }

      const thirtyDaysAgo = new Date();
      thirtyDaysAgo.setDate(thirtyDaysAgo.getDate() - 30);
      setFromDate(thirtyDaysAgo.toISOString().split('T')[0]);
      setToDate(new Date().toISOString().split('T')[0]);
    }
  }, []);

  useEffect(() => {
    if (router.query.productId && typeof router.query.productId === 'string') {
      router.push(`/vendor/product-sales/${router.query.productId}`);
    }
  }, [router.query.productId]);

  useEffect(() => {
    const timer = setTimeout(() => setDebouncedSearch(search), 500);
    return () => clearTimeout(timer);
  }, [search]);

  const fetchAnalytics = useCallback(async () => {
    if (!vendorId) return;

    setLoading(true);
    setError('');

    try {
      const params: any = {
        from: fromDate,
        to: toDate,
        search: debouncedSearch,
        category,
        stockStatus,
        minUnits: minUnits ? Number(minUnits) : undefined,
        minRevenue: minRevenue ? Number(minRevenue) : undefined,
        zeroSalesOnly,
        sortBy,
        sortOrder,
        page,
        limit,
      };

      const result = await api.productSales.getAnalytics(vendorId, params);
      setData(result);
    } catch (err: any) {
      setError(err.message || 'Failed to load analytics');
      pushToast({ type: 'error', title: 'Load Failed', message: err.message });
    } finally {
      setLoading(false);
    }
  }, [vendorId, fromDate, toDate, debouncedSearch, category, stockStatus, minUnits, minRevenue, zeroSalesOnly, sortBy, sortOrder, page, limit, pushToast]);

  useEffect(() => {
    if (vendorId && fromDate && toDate) {
      fetchAnalytics();
    }
  }, [fetchAnalytics, vendorId, fromDate, toDate]);

  const handleSort = (column: string) => {
    if (sortBy === column) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setSortBy(column);
      setSortOrder('desc');
    }
    setPage(1);
  };

  const handleRowClick = (productId: string) => {
    router.push(`/vendor/product-sales/${productId}`);
  };

  const categories = useMemo(() => {
    if (!data?.products) return [];
    const cats = new Set(data.products.map(p => p.category).filter(Boolean));
    return Array.from(cats);
  }, [data]);

  const renderKPIs = () => {
    if (loading && !data) {
      return (
        <div className="ps-kpi-grid">
          {[1, 2, 3, 4, 5].map(i => (
            <div key={i} className="ps-kpi-card">
              <div className="ps-skeleton ps-skeleton-card"></div>
            </div>
          ))}
        </div>
      );
    }

    if (!data?.kpis) return null;

    const kpis = data.kpis;

    return (
      <div className="ps-kpi-grid">
        <div className="ps-kpi-card">
          <div className="ps-kpi-label">Total Units Sold</div>
          <div className="ps-kpi-value">{kpis.totalUnitsSold.toLocaleString()}</div>
        </div>
        <div className="ps-kpi-card">
          <div className="ps-kpi-label">Total Revenue</div>
          <div className="ps-kpi-value">${kpis.totalRevenue.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</div>
        </div>
        <div className="ps-kpi-card">
          <div className="ps-kpi-label">Best Selling Product</div>
          <div className="ps-kpi-value" style={{ fontSize: '1.25rem' }}>{kpis.bestSellingProduct || '—'}</div>
        </div>
        <div className="ps-kpi-card">
          <div className="ps-kpi-label">Zero-Sales Products</div>
          <div className="ps-kpi-value">{kpis.zeroSalesProducts}</div>
        </div>
        <div className="ps-kpi-card">
          <div className="ps-kpi-label">Cancellation Rate</div>
          <div className="ps-kpi-value">{(kpis.cancellationRate * 100).toFixed(1)}%</div>
        </div>
      </div>
    );
  };

  const renderTable = () => {
    if (loading && !data) {
      return (
        <div className="ps-table-section">
          <div className="ps-table-wrapper">
            {[1, 2, 3, 4, 5].map(i => (
              <div key={i} className="ps-skeleton ps-skeleton-row"></div>
            ))}
          </div>
        </div>
      );
    }

    if (error) {
      return (
        <div className="ps-error">
          <strong>Error:</strong> {error}
        </div>
      );
    }

    if (!data?.products || data.products.length === 0) {
      return (
        <div className="ps-table-section">
          <div className="ps-empty-state">
            <div className="ps-empty-icon">📦</div>
            <div className="ps-empty-title">No product sales found</div>
            <p>Try adjusting your filters or date range</p>
          </div>
        </div>
      );
    }

    return (
      <div className="ps-table-section">
        <div className="ps-table-wrapper">
          <table className="ps-table">
            <thead>
              <tr>
                <th className="sortable" onClick={() => handleSort('productName')}>
                  Product Name {sortBy === 'productName' && (sortOrder === 'asc' ? '↑' : '↓')}
                </th>
                <th className="sortable" onClick={() => handleSort('category')}>
                  Category {sortBy === 'category' && (sortOrder === 'asc' ? '↑' : '↓')}
                </th>
                <th className="sortable" onClick={() => handleSort('unitsSold')}>
                  Units Sold {sortBy === 'unitsSold' && (sortOrder === 'asc' ? '↑' : '↓')}
                </th>
                <th className="sortable" onClick={() => handleSort('revenue')}>
                  Revenue {sortBy === 'revenue' && (sortOrder === 'asc' ? '↑' : '↓')}
                </th>
                <th className="sortable" onClick={() => handleSort('avgSellingPrice')}>
                  Avg Price {sortBy === 'avgSellingPrice' && (sortOrder === 'asc' ? '↑' : '↓')}
                </th>
                <th className="sortable" onClick={() => handleSort('ordersCount')}>
                  Orders {sortBy === 'ordersCount' && (sortOrder === 'asc' ? '↑' : '↓')}
                </th>
                <th className="sortable" onClick={() => handleSort('currentStock')}>
                  Stock {sortBy === 'currentStock' && (sortOrder === 'asc' ? '↑' : '↓')}
                </th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody>
              {data.products.map((product) => (
                <tr key={product.productId} onClick={() => handleRowClick(product.productId)}>
                  <td>
                    <span className="ps-product-name">{product.productName}</span>
                  </td>
                  <td>{product.category || '—'}</td>
                  <td>{product.unitsSold.toLocaleString()}</td>
                  <td>${product.revenue.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
                  <td>${product.avgSellingPrice.toFixed(2)}</td>
                  <td>{product.ordersCount}</td>
                  <td>{product.currentStock}</td>
                  <td>
                    <span className={`ps-status-badge ${product.status}`}>
                      {product.status === 'in-stock' && 'In Stock'}
                      {product.status === 'low-stock' && 'Low Stock'}
                      {product.status === 'out-of-stock' && 'Out of Stock'}
                    </span>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        {data.pagination && (
          <div className="ps-pagination">
            <div className="ps-pagination-info">
              Showing {((page - 1) * limit) + 1}-{Math.min(page * limit, data.pagination.total)} of {data.pagination.total} products
            </div>
            <div className="ps-pagination-controls">
              <button
                className="ps-pagination-btn"
                disabled={page === 1}
                onClick={() => setPage(page - 1)}
              >
                Previous
              </button>
              <button
                className="ps-pagination-btn"
                disabled={page >= data.pagination.pages}
                onClick={() => setPage(page + 1)}
              >
                Next
              </button>
            </div>
          </div>
        )}
      </div>
    );
  };

  return (
    <DashboardLayout role="vendor">
      <div className="product-sales-page">
        <div className="ps-header">
          <h1 className="ps-title">Product Sales Analytics</h1>
          <div className="ps-date-selector">
            <label htmlFor="from-date">From:</label>
            <input
              id="from-date"
              type="date"
              className="ps-date-input"
              value={fromDate}
              onChange={(e) => { setFromDate(e.target.value); setPage(1); }}
            />
            <label htmlFor="to-date">To:</label>
            <input
              id="to-date"
              type="date"
              className="ps-date-input"
              value={toDate}
              onChange={(e) => { setToDate(e.target.value); setPage(1); }}
            />
          </div>
        </div>

        {renderKPIs()}

        <div className="ps-filters-section">
          <div className="ps-search-row">
            <input
              type="text"
              className="ps-search-input"
              placeholder="Search by product name, SKU, or category..."
              value={search}
              onChange={(e) => { setSearch(e.target.value); setPage(1); }}
            />
          </div>
          <div className="ps-filters-grid">
            <div className="ps-filter-group">
              <label className="ps-filter-label" htmlFor="category-filter">Category</label>
              <select
                id="category-filter"
                className="ps-filter-select"
                value={category}
                onChange={(e) => { setCategory(e.target.value); setPage(1); }}
              >
                <option value="">All Categories</option>
                {categories.map(cat => (
                  <option key={cat} value={cat}>{cat}</option>
                ))}
              </select>
            </div>
            <div className="ps-filter-group">
              <label className="ps-filter-label" htmlFor="stock-filter">Stock Status</label>
              <select
                id="stock-filter"
                className="ps-filter-select"
                value={stockStatus}
                onChange={(e) => { setStockStatus(e.target.value); setPage(1); }}
              >
                <option value="">All</option>
                <option value="in-stock">In Stock</option>
                <option value="low-stock">Low Stock</option>
                <option value="out-of-stock">Out of Stock</option>
              </select>
            </div>
            <div className="ps-filter-group">
              <label className="ps-filter-label" htmlFor="min-units">Min Units Sold</label>
              <input
                id="min-units"
                type="number"
                className="ps-filter-input"
                placeholder="0"
                value={minUnits}
                onChange={(e) => { setMinUnits(e.target.value); setPage(1); }}
              />
            </div>
            <div className="ps-filter-group">
              <label className="ps-filter-label" htmlFor="min-revenue">Min Revenue</label>
              <input
                id="min-revenue"
                type="number"
                className="ps-filter-input"
                placeholder="0"
                value={minRevenue}
                onChange={(e) => { setMinRevenue(e.target.value); setPage(1); }}
              />
            </div>
          </div>
          <div className="ps-checkbox-group">
            <input
              id="zero-sales"
              type="checkbox"
              className="ps-checkbox"
              checked={zeroSalesOnly}
              onChange={(e) => { setZeroSalesOnly(e.target.checked); setPage(1); }}
            />
            <label htmlFor="zero-sales" className="ps-filter-label">Show zero-sales products only</label>
          </div>
        </div>

        {renderTable()}
      </div>
    </DashboardLayout>
  );
}
